* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
}

form {
    display: flex;
    flex-flow: column;
}

button {
    padding: 5px 15px;
    border-radius: 5px;
    background: white;
    font-family: inherit;
    border: 1px solid black;
    cursor: pointer;
    font-size: 15px;
}

button:last-child {
    margin-top: 20px;
}

label {
    display: flex;
    flex-flow: row;
    align-items: center;
    margin: 10px 0;
}

span {
    flex: 0 0 50px;
    margin-right: 10px;
}

input:not([type="color"]) {
    padding: 5px 15px;
    font-family: inherit;
}

h1 {
    font-size: 40px;
    margin-bottom: 25px;
}

h5 {
    font-size: 25px;
    margin-bottom: 25px;
}

h6 {
    font-size: 20px;
    margin-bottom: 25px;
}

.app {
    padding: 20px;
}

.create-rect {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.3);
    height: 100%;
    width: 100%;
}

.create-rect-modal {
    border-radius: 5px;
    display: flex;
    flex-flow: column;
    padding: 20px;
    background: white;
    width: 400px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.canvas {
    width: 500px;
    height: 500px;
    border: 1px dashed black;
    margin-top: 20px;
}

.controls {
    position: fixed;
    background: white;
    z-index: 1;
    top: 0;
    right: 0;
    width: 300px;
    height: 100%;
    padding: 20px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.controls-form {
    display: flex;
    flex-flow: column;
}